import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";
import AnchorLink from "react-anchor-link-smooth-scroll";

function Header() {
  return (
    <header className="header">
      <div className="container">
        <div className="header__inner">
          <Link to="/" className="logo">
            <img src="images/logo.jpg" alt="Logo" className="logo__img" />
          </Link>
          <nav className="menu">
            <ul className="menu__list">
              <li className="menu__item">
                <AnchorLink href="#info" className="menu__link">
                  
                </AnchorLink>
              </li>
              <li className="menu__item">
                <AnchorLink href="#characteristics" className="menu__link">
                  
                </AnchorLink>
              </li>
              <li className="menu__item">
                <AnchorLink href="#price" className="menu__link">
                  
                </AnchorLink>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </header>
  );
}

export default Header;
