import React, { useReducer, useState } from "react";
import BikeItem from "./BikeItem";
import Modal from "./Modal";

import "./Price.css";
function Price({ bike }) {
  const [checkbike, setCheckBike] = useState(1);
  const [firstName, setFirstName] = useState("");
  const [phoneNum, setPhoneNum] = useState("");
  const [modal, setModal] = useReducer((modal) => !modal, false);
  const checkItem = (id) => {
    setCheckBike(id);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    setModal();
  };
  return (
    <section id="price" className="price">
      <div className="container">
        <div className="price__inner">
          <form onSubmit={handleSubmit} action="" className="form">
            <h4 className="form__title"></h4>
            <p className="form__text">
                  ,     
                   
            </p>
            <input
              id="name"
              type="text"
              className="form__input-name"
              placeholder=""
              onChange={(e) => setFirstName(e.target.value)}
            />
            <input
              id="phone"
              type="tel"
              className="form__input-phone"
              placeholder=""
              onChange={(e) => setPhoneNum(e.target.value)}
            />
            <p className="form__input-text">  </p>
            <div className="form__label-box">
              {bike.map((el) => (
                <label
                  key={el.id}
                  onClick={() => checkItem(el.id)}
                  className="form__label">
                  <input
                    className="form__input-radio"
                    type="radio"
                    name="radio"
                  />
                  <div
                    style={{ background: el.color }}
                    className="form__radio-style"></div>
                </label>
              ))}

              <BikeItem bike={bike} check={checkbike} />
            </div>
            <button className="form__btn" type="submit">
              
            </button>
          </form>
        </div>
      </div>
      {modal && (
        <Modal
          checkbike={checkbike}
          firstName={firstName}
          phoneNum={phoneNum}
          bike={bike}
          modalCheck={(modal) => setModal(modal)}
        />
      )}
    </section>
  );
}

export default Price;
