import React from "react";
import { IoMdClose } from "react-icons/io";
import "./Modal.css";
function Modal({ name, phone, checkbike, bike, modalCheck }) {
  const closeModal = () => {
    modalCheck(false);
  };

  return (
    <div className="modal">
      <div className="modal__inner">
        <div className="modal__header">
          <IoMdClose className="modal__close" onClick={closeModal} />
        </div>
        <h2 className="modal__title">
                !
        </h2>

        {bike.map((el) => {
          if (el.id === checkbike) {
            return (
              <div key={el.id} className="modal__bike">
                <p className="modal__name"> : {el.name}</p>
                <p className="modal__name">: {el.price}</p>
                <div
                  className="modal__color"
                  style={{ background: el.color }}></div>
              </div>
            );
          }
        })}
      </div>
    </div>
  );
}

export default Modal;
