import React from "react";
import { Link } from "react-router-dom";
import "./Footer.css";

function Footer() {
  return (
    <footer className="footer">
      <div className="container">
        <div className="footer__inner">
          <Link to="/" className="footer__link">
             
          </Link>
          <div className="footer__social">
            <Link to="" className="footer__social-link">
              <img
                src="images/instagram.svg"
                alt=""
                className="footer__social-img"
              />
            </Link>
            <Link to="" className="footer__social-link">
              <img
                src="images/facebook.svg"
                alt=""
                className="footer__social-img"
              />
            </Link>
          </div>
          <div className="footer__contacts">
            <Link className="footer__contacts-link" to="tel:+380506777777">
              <img
                src="images/phone.svg"
                alt=""
                className="footer__contacts-img"
              />
              <span> +38 050 677 77 77</span>
            </Link>
            <Link
              className="footer__contacts-link"
              to="mailto:elektrobikeukr@gmail.com">
              <img
                src="images/email.svg"
                alt=""
                className="footer__contacts-img"
              />
              <span>elektrobikeukr@gmail.com </span>
            </Link>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
