import React from "react";

function BikeItem({ bike, check }) {
  return (
    <div className="form__bike">
      {bike.map((el) => {
        if (el.id === check) {
          return (
            <div key={el.id}>
              <img
                className="form__bike-img"
                src={`images/${el.img}`}
                alt="Bike"
              />
              <p className="form__bike-price">
                <span>{el.price} . </span>
              </p>
            </div>
          );
        }
      })}
    </div>
  );
}

export default BikeItem;
