import React from "react";
import Slider from "react-slick";
import "./SliderBike.css";
function SliderBike() {
  const settings = {
    dots: true,
    fade: true,
    infinite: true,
    arrows: false,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 2000,
    cssEase: "linear",
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  return (
    <div className="slider">
      <div className="container">
        <Slider className="slider__items" {...settings}>
          <div className="slider__item">
            <img
              src="images/slider-1.jpg"
              alt="Slider1"
              className="slider__img"
            />
          </div>
          <div className="slider__item">
            <img
              src="images/slider-2.jpg"
              alt="Slider2"
              className="slider__img"
            />
          </div>
          <div className="slider__item">
            <img
              src="images/slider-3.jpg"
              alt="Slider3"
              className="slider__img"
            />
          </div>
        </Slider>
      </div>
    </div>
  );
}

export default SliderBike;
