import React from "react";

import Slider from "react-slick";
import AnchorLink from "react-anchor-link-smooth-scroll";
import "./Bike.css";
function Bike() {
  const settings = {
    dots: true,
    arrows: false,
    fade: true,
    infinite: true,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 2000,
    cssEase: "linear",
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <section className="bike">
      <div className="container">
        <div className="bike__inner">
          <div className="bike__column">
            <h1 className="bike__title">Xiaomi Himo C26</h1>
            <h3 className="bike__subtitle"> </h3>
            <p className="bike__text">
                     
                
            </p>
            <p className="bike__text">
              HIMO C26      Xiaomi,  
                  ,   .   
                 ,   
                   100 ,   ,  
               .     
              ,  , 7-   
                   .{" "}
            </p>
            <AnchorLink className="bike__link" href="#price">
              
            </AnchorLink>
          </div>
          <div className="bike__column bike__column-slider">
            <Slider className="bike__slider" {...settings}>
              <img
                src="images/bike-1.jpg"
                alt=""
                className="bike-slider__item"
              />
              <img
                src="images/bike-2.jpg"
                alt=""
                className="bike-slider__item"
              />
              <img
                src="images/bike-3.jpg"
                alt=""
                className="bike-slider__item"
              />
              <img
                src="images/bike-4.jpg"
                alt=""
                className="bike-slider__item"
              />
            </Slider>
            <ul className="bike__list">
              <li className="bike__list-item">
                <span className="bike__list-num">  100 </span>{" "}
                <span className="bike__list-text"> </span>
              </li>
              <li className="bike__list-item">
                <span className="bike__list-num">25 / </span>{" "}
                <span className="bike__list-text"> </span>
              </li>
              <li className="bike__list-item">
                <span className="bike__list-num">3   </span>
                <span className="bike__list-text">
                  , , 
                </span>
              </li>
              <li className="bike__list-item">
                <span className="bike__list-num">25  </span>{" "}
                <span className="bike__list-text"></span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Bike;
